<template lang="pug">
#info
  .tag 
  el-form.ruleForm(:model="ruleForm", :rules="rules", ref="ruleForm", label-width="100px")
    el-form-item(label="", prop="name")
      el-input(v-model="ruleForm.name", placeholder="5")
    el-form-item(label="", prop="place")
      el-input(v-model="ruleForm.place", placeholder="")
    el-form-item(label="", prop="timeRange")
      el-date-picker(v-model="ruleForm.timeRange", type="datetimerange", placeholder="", align="right")
    el-form-item(label="", prop="upload")
      el-upload.upload(drag, action="//jsonplaceholder.typicode.com/posts/")
        i.el-icon-upload
        .el-upload__text <em></em>
        .el-upload__tip(slot="tip") 2Mjpg, gif, png, bmp1080*640
    el-form-item(label="", prop="people")
      el-input(v-model.number="ruleForm.people")
    el-form-item(label="", prop="desc")
      el-input(type="textarea", v-model="ruleForm.desc")
    el-form-item
      .subtitle 
      .colleges
        span(v-for="(item, index) of colleges")
          input(:id="item.id", type="checkbox", v-model="item.toggle")
          label(:for="item.id") {{ item.title }}
      .subtitle 
      el-input(type="text", v-model="ruleForm.wechat")
      .subtitle 
      el-input(type="text", v-model="ruleForm.condition")
    el-form-item
      el-button.submitButton(type="danger", size="large", @click="submitForm('ruleForm')") 
</template>

<script>
export default {
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (Number(this.ruleForm.people) < 2) {
            alert('2')
            return false
          }
          let allData = this.ruleForm
          for (let item of this.colleges) {
            if (item.toggle) {
              console.log(item.title)
              allData.colleges.push(item.u_id)
            }
          }
          if (!allData.colleges.length) {
            alert('1')
            return false
          }
          this.$store.dispatch('BasicInfo', allData)
          this.$router.push('member')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  },
  data () {
    return {
      colleges: [
        {
          title: '',
          u_id: 1,
          id: 'tsinghua',
          toggle: false
        },
        {
          title: '',
          u_id: 2,
          id: 'bfjd',
          toggle: false
        },
        {
          title: '',
          u_id: 3,
          id: 'gebi',
          toggle: false
        }
      ],
      ruleForm: {
        name: '',       //  // title
        place: '',      //  // location_string
        people: '',     //  () () // participants
        desc: '',       //  // discription
        wechat: '',     //  ()
        condition: '',  //  () // welcome
        colleges: [],    //  // advertising
        timeRange: ''
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, message: '5', trigger: 'blur' }
        ],
        place: [
          { required: true, message: '', trigger: 'blur' }
        ],
        people: [
          { required: true, type: 'number', message: '', trigger: 'change' }
        ],
        desc: [
          { required: true, message: '', trigger: 'blur' }
        ],
        timeRange: [
          { required: true, message: '' }
        ]
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
#info
  .submitButton
    padding 10px 100px
  .upload
    text-align left
  .subtitle
    text-align left
    margin-top 15px
  .ruleForm
    text-align left
    .colleges input
      display none

    .colleges label
      cursor pointer
      background #eee
      border 1px solid #ccc
      padding 5px 20px
      margin-right 20px

    .colleges input:checked ~ label
      background red
      color white
</style>



